









import { Component, Prop, Vue } from "vue-property-decorator";
import SuperHero from "../entities/SuperHero";

@Component
export default class SuperheroCard extends Vue {
  @Prop({ required: true })
  superhero!: SuperHero;
  goToDetail() {
    this.$router.push("/characters/" + this.superhero.id);
  }
}



